import React, { memo, useContext, useEffect, useState } from "react";
import PropTypes from "prop-types";
import PopupWithForm from "./PopupWithForm";
import CurrentUserContext from "../contexts/CurrentUserContext";

const EditProfilePopup = ({ onUpdateUser, isOpen, onClose, isLoading }) => {
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const currentUser = useContext(CurrentUserContext);

  useEffect(() => {
    setName(currentUser.name);
    setDescription(currentUser.about);
  }, [currentUser, isOpen]);

  function handleSubmit(e) {
    e.preventDefault();

    onUpdateUser({
      name: name,
      about: description,
    });
  }

  return (
    <PopupWithForm
      title="Редактировать профиль"
      name="popup_edit-profile"
      buttonName={isLoading ? "Сохраняется..." : "Сохранить"}
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
    >
      <>
        <div className="popup__inner">
          <label htmlFor="profile-name-input">
            <input
              id="profile-name-input"
              type="text"
              name="name"
              value={name}
              placeholder="Имя"
              className="popup__input popup__input_name"
              required
              minLength="2"
              maxLength="40"
              onChange={(e) => setName(e.target.value)}
            />
            <span
              id="profile-name-input-error"
              className="popup__input-error"
            />
          </label>
        </div>
        <div className="popup__inner">
          <label htmlFor="profile-job-input">
            <input
              id="profile-job-input"
              type="text"
              name="job"
              value={description}
              placeholder="Профессия"
              className="popup__input popup__input_job"
              required
              minLength="2"
              maxLength="200"
              onChange={(e) => setDescription(e.target.value)}
            />
            <span id="profile-job-input-error" className="popup__input-error" />
          </label>
        </div>
      </>
    </PopupWithForm>
  );
};

EditProfilePopup.propTypes = {
  isOpen: PropTypes.bool,
  isLoading: PropTypes.bool,
  onClose: PropTypes.func,
  onUpdateUser: PropTypes.func,
  handleSubmit: PropTypes.func,
};

function areEqual(prevProps, nextProps) {
  return (
    nextProps.isOpen === prevProps.isOpen &&
    nextProps.isLoading === prevProps.isLoading
  );
}

export default memo(EditProfilePopup, areEqual);
