import React, { memo, useEffect, useState } from "react";
import PopupWithForm from "./PopupWithForm";
import PropTypes from "prop-types";

const AddPlacePopup = ({ onAddPlace, isOpen, onClose, isLoading }) => {
  const [name, setName] = useState("");
  const [link, setLink] = useState("");

  useEffect(() => {
    setName("");
    setLink("");
  }, [isOpen]);

  function handleSubmit(e) {
    e.preventDefault();

    onAddPlace({
      name: name,
      link: link,
    });
  }

  return (
    <PopupWithForm
      title="Добавить место"
      name="popup_add-photo"
      buttonName={isLoading ? "Добавляется..." : "Добавить"}
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
    >
      <>
        <div className="popup__inner">
          <label htmlFor="photo-name-input">
            <input
              id="photo-name-input"
              type="text"
              name="name"
              value={name}
              placeholder="Название"
              className="popup__input popup__input_name"
              required
              minLength="2"
              maxLength="30"
              onChange={(e) => setName(e.target.value)}
            />
            <span id="photo-name-input-error" className="popup__input-error" />
          </label>
        </div>
        <div className="popup__inner">
          <label htmlFor="photo-link-input">
            <input
              id="photo-link-input"
              type="url"
              name="link"
              value={link}
              placeholder="Ссылка на картинку"
              className="popup__input popup__input_link"
              required
              onChange={(e) => setLink(e.target.value)}
            />
            <span id="photo-link-input-error" className="popup__input-error" />
          </label>
        </div>
      </>
    </PopupWithForm>
  );
};

AddPlacePopup.propTypes = {
  isOpen: PropTypes.bool,
  isLoading: PropTypes.bool,
  onClose: PropTypes.func,
  onAddPlace: PropTypes.func,
};

function areEqual(prevProps, nextProps) {
  return (
    nextProps.isOpen === prevProps.isOpen &&
    nextProps.isLoading === prevProps.isLoading
  );
}

export default memo(AddPlacePopup, areEqual);
