import React, { memo, useContext } from "react";
import PropTypes from "prop-types";
import CurrentUserContext from "../contexts/CurrentUserContext";

const Card = ({ onCardClick, onCardLike, onCardDelete, ...card }) => {
  const currentUser = useContext(CurrentUserContext);

  const isOwn = card.owner._id === currentUser._id;
  const isLiked = card.likes.some((i) => i._id === currentUser._id);

  function handleLikeClick() {
    onCardLike(card);
  }

  function handleDeleteClick() {
    onCardDelete(card);
  }

  function handleClick() {
    onCardClick(card);
  }

  return (
    <li className="photos__item">
      <img
        src={card.link}
        alt={card.alt}
        className="photos__image"
        onClick={handleClick}
      />
      <h2 className="photos__title">{card.name}</h2>
      {isOwn && (
        <button
          type="button"
          aria-label="Удалить"
          className="photos__delete"
          onClick={handleDeleteClick}
        />
      )}
      <button
        type="button"
        aria-label="Избранное"
        className={`photos__favorite ${
          isLiked ? "photos__favorite_active" : ""
        }`}
        onClick={handleLikeClick}
      />
      <span className="photos__likes">{card.likes.length}</span>
    </li>
  );
};

Card.propTypes = {
  card: PropTypes.shape({
    name: PropTypes.string,
    link: PropTypes.string,
    alt: PropTypes.string,
    likes: PropTypes.array,
    owner: PropTypes.string,
  }),
  onCardClick: PropTypes.func,
  onCardLike: PropTypes.func,
  onCardDelete: PropTypes.func,
};

function areEqual(prevProps, nextProps) {
  return nextProps.likes.length === prevProps.likes.length;
}

export default memo(Card, areEqual);
