import React, { useContext, useEffect, useRef, memo } from "react";
import PropTypes from "prop-types";
import PopupWithForm from "./PopupWithForm";
import CurrentUserContext from "../contexts/CurrentUserContext";

const EditAvatarPopup = ({ onUpdateAvatar, isOpen, onClose, isLoading }) => {
  const currentUser = useContext(CurrentUserContext);
  const avatarRef = useRef();

  useEffect(() => {
    avatarRef.current.value = currentUser.avatar;
  }, [currentUser]);

  function handleSubmit(e) {
    e.preventDefault();

    onUpdateAvatar({
      avatar: avatarRef.current.value,
    });
  }

  return (
    <PopupWithForm
      title="Редактировать аватар"
      name="popup_edit-profile-avatar"
      buttonName={isLoading ? "Сохраняется..." : "Сохранить"}
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
    >
      <div className="popup__inner">
        <label htmlFor="profile-link-input">
          <input
            id="profile-link-input"
            type="url"
            name="link"
            ref={avatarRef}
            placeholder="Ссылка на картинку"
            className="popup__input popup__input_link"
            required
            onChange={(e) => (avatarRef.current.value = e.target.value)}
          />
          <span id="profile-link-input-error" className="popup__input-error" />
        </label>
      </div>
    </PopupWithForm>
  );
};

EditAvatarPopup.propTypes = {
  isOpen: PropTypes.bool,
  isLoading: PropTypes.bool,
  onClose: PropTypes.func,
  onUpdateAvatar: PropTypes.func,
};

function areEqual(prevProps, nextProps) {
  return (
    nextProps.isOpen === prevProps.isOpen &&
    nextProps.isLoading === prevProps.isLoading
  );
}

export default memo(EditAvatarPopup, areEqual);
