import React, { useEffect, useState } from "react";
import Header from "./Header";
import Main from "./Main";
import Footer from "./Footer";
import ImagePopup from "./ImagePopup";
import EditProfilePopup from "./EditProfilePopup";
import EditAvatarPopup from "./EditAvatarPopup";
import AddPlacePopup from "./AddPlacePopup";
import DeletePlacePopup from "./DeletePlacePopup";
import CurrentUserContext from "../contexts/CurrentUserContext";
import api from "../utils/api";
import "../index.css";

export default function App() {
  const [isEditAvatarPopupOpen, setEditAvatarPopupOpen] = useState(false);
  const [isEditProfilePopupOpen, setEditProfilePopupOpen] = useState(false);
  const [isAddPlacePopupOpen, setAddPlacePopupOpen] = useState(false);
  const [isViewPlacePopupOpen, setViewPlacePopupOpen] = useState(false);
  const [isDeletePlacePopupOpen, setDeletePlacePopupOpen] = useState(false);

  const [selectedCard, setSelectedCard] = useState({});
  const [currentUser, setCurrentUser] = useState({
    name: "",
    about: "",
    avatar: "",
  });
  const [cards, setCards] = useState([]);
  const [isLoading, setLoading] = useState(false);

  useEffect(() => {
    Promise.all([api.getUserInfo(), api.getInitialCards()])
      .then(([currentUser, items]) => {
        setCurrentUser(currentUser);
        setCards(items);
      })
      .catch(({ message }) => console.log(message));
  }, []);

  function closeAllPopups() {
    setEditProfilePopupOpen(false);
    setAddPlacePopupOpen(false);
    setDeletePlacePopupOpen(false);
    setViewPlacePopupOpen(false);
    setEditAvatarPopupOpen(false);
  }

  function handleCardLike(card) {
    const isLiked = card.likes.some((i) => i._id === currentUser._id);
    api
      .changeLikeStatus(card._id, isLiked)
      .then((newCard) => {
        setCards((prevCards) => {
          return prevCards.map((c) => (c._id === card._id ? newCard : c));
        });
      })
      .catch(({ message }) => console.log(message));
  }

  function handleCardClick(props) {
    setSelectedCard(props);
    setViewPlacePopupOpen(true);
  }

  function handleUpdateUser(data) {
    setLoading(true);

    api
      .setUserInfo(data)
      .then(setCurrentUser)
      .then(closeAllPopups)
      .catch(({ message }) => console.log(message))
      .finally(() => setLoading(false));
  }

  function handleAddPlace(data) {
    setLoading(true);

    return api
      .addItem(data)
      .then((card) => {
        setCards((prevCards) => {
          return [card, ...prevCards];
        });
      })
      .then(closeAllPopups)
      .catch(({ message }) => console.log(message))
      .finally(() => setLoading(false));
  }

  function handleDeletePlace(card) {
    setLoading(true);

    return api
      .deleteItem(card._id)
      .then(() => {
        setCards(cards.filter((item) => item._id !== card._id));
      })
      .then(closeAllPopups)
      .catch(({ message }) => console.log(message))
      .finally(() => setLoading(false));
  }

  function handleUpdateAvatar(data) {
    setLoading(true);

    return api
      .setUserAvatar(data)
      .then(setCurrentUser)
      .then(closeAllPopups)
      .catch(({ message }) => console.log(message))
      .finally(() => setLoading(false));
  }

  function handleEditProfileClick() {
    setEditProfilePopupOpen(true);
  }

  function handleEditAvatarClick() {
    setEditAvatarPopupOpen(true);
  }

  function handleAddPlaceClick() {
    setAddPlacePopupOpen(true);
  }

  function handleDeletePlaceClick(props) {
    setSelectedCard(props);
    setDeletePlacePopupOpen(true);
  }

  return (
    <CurrentUserContext.Provider value={currentUser}>
      <div className="page">
        <div className="page__container">
          <Header />
          <Main
            onEditProfile={handleEditProfileClick}
            onAddPlace={handleAddPlaceClick}
            onCardDelete={handleDeletePlaceClick}
            onEditAvatar={handleEditAvatarClick}
            onCardClick={handleCardClick}
            cards={cards}
            onCardLike={handleCardLike}
            onDeleteCard={handleDeletePlace}
          />
          <EditProfilePopup
            isOpen={isEditProfilePopupOpen}
            onClose={closeAllPopups}
            onUpdateUser={handleUpdateUser}
            isLoading={isLoading}
          />
          <AddPlacePopup
            isOpen={isAddPlacePopupOpen}
            onClose={closeAllPopups}
            onAddPlace={handleAddPlace}
            isLoading={isLoading}
          />
          <EditAvatarPopup
            isOpen={isEditAvatarPopupOpen}
            onClose={closeAllPopups}
            onUpdateAvatar={handleUpdateAvatar}
            isLoading={isLoading}
          />
          <DeletePlacePopup
            isOpen={isDeletePlacePopupOpen}
            onClose={closeAllPopups}
            onDeletePlace={handleDeletePlace}
            card={selectedCard}
            isLoading={isLoading}
          />
          <ImagePopup
            isOpen={isViewPlacePopupOpen}
            onClose={closeAllPopups}
            card={selectedCard}
          />
          <Footer />
        </div>
      </div>
    </CurrentUserContext.Provider>
  );
}
