const apiToken = "a30ed4a1-f0e8-4bfe-830c-56dd94af5aeb";
const apiGroupId = 16;
const apiBaseUrl = `https://mesto.nomoreparties.co/v1/cohort-${apiGroupId}`;

class api {
  constructor(config) {
    this._baseUrl = config.baseUrl;
    this._headers = config.headers;
  }

  getUserInfo() {
    return fetch(`${this._baseUrl}/users/me`, {
      headers: this._headers,
    }).then((res) => this._convert(res, `getUserInfo: ${res.status}`));
  }

  setUserInfo(data) {
    return fetch(`${this._baseUrl}/users/me`, {
      method: "PATCH",
      headers: this._headers,
      body: JSON.stringify(data),
    }).then((res) => this._convert(res, `setUserInfo: ${res.status}`));
  }

  setUserAvatar(data) {
    return fetch(`${this._baseUrl}/users/me/avatar`, {
      method: "PATCH",
      headers: this._headers,
      body: JSON.stringify({
        avatar: data.avatar,
      }),
    }).then((res) => this._convert(res, `setUserAvatar: ${res.status}`));
  }

  addItem(data) {
    return fetch(`${this._baseUrl}/cards`, {
      method: "POST",
      headers: this._headers,
      body: JSON.stringify(data),
    }).then((res) => this._convert(res, `addItem: ${res.status}`));
  }

  deleteItem(cardId) {
    return fetch(`${this._baseUrl}/cards/${cardId}`, {
      method: "DELETE",
      headers: this._headers,
    }).then((res) => this._convert(res, `deleteItem: ${res.status}`));
  }

  getInitialCards() {
    return fetch(`${this._baseUrl}/cards`, {
      headers: this._headers,
    }).then((res) => this._convert(res, `getInitialCards: ${res.status}`));
  }

  addLike(cardId) {
    return fetch(`${this._baseUrl}/cards/likes/${cardId}`, {
      method: "PUT",
      headers: this._headers,
    }).then((res) => this._convert(res, `addLike: ${res.status}`));
  }

  deleteLike(cardId) {
    return fetch(`${this._baseUrl}/cards/likes/${cardId}`, {
      method: "DELETE",
      headers: this._headers,
    }).then((res) => this._convert(res, `deleteLike: ${res.status}`));
  }

  changeLikeStatus(cardId, isLiked) {
    return isLiked ? this.deleteLike(cardId) : this.addLike(cardId);
  }

  _convert(res, err) {
    if (res.ok) {
      return res.json();
    }

    return Promise.reject(`Ошибка: ${err}`);
  }
}

export default new api({
  baseUrl: apiBaseUrl,
  headers: {
    authorization: apiToken,
    "Content-Type": "application/json",
  },
});
